/* Styling for Status */
.status-tag{
    position: absolute;
    color: white;
    display: block;
    background-color: #AE4DFF;
    text-align: center;
    right: -5%;
    top: -30px;
    height: 65px;
    width: 65px;
    border-radius: 50%;
    box-sizing: border-box;
    font-size: 2rem;
    padding-top: 7px;
    text-transform: uppercase;
    padding-left: 0.3rem;
    letter-spacing: 0.13em;
    transition: all 0.5s ease;
}

.status-tag-1{
    position: absolute;
    color: white;
    display: block;
    background-color:#733380;
    text-align: center;
    right: -5%;
    top: -30px;
    height: 65px;
    width: 65px;
    border-radius: 50%;
    box-sizing: border-box;
    font-size: 2rem;
    padding-top: 7px;
    padding-left: 0.3rem;
    text-transform: uppercase;
    letter-spacing: 0.13em;
    transition: all 0.5s ease;
    
}


